.header {
  margin-top: 20px;
  padding: 20px 40px;
  border-radius: 20px 20px 0 0;
  background-color: rgb(68, 0, 204);
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(28, 210, 242)), to(rgb(68, 0, 204)));
  background-image: -webkit-linear-gradient(rgb(28, 210, 242), rgb(68, 0, 204));
  background-image: -o-linear-gradient(rgb(28, 210, 242), rgb(68, 0, 204));
  background-image: linear-gradient(rgb(28, 210, 242), rgb(68, 0, 204));
  color: rgb(255, 255, 255);
}



